@font-face {
  font-family: "Comfortaa";
  src: url("../fonts/Comfortaa-Bold.woff2") format("woff2"), url("../fonts/Comfortaa-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Comfortaa";
  src: url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
body {
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: "Roboto", Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  color: #000000;
  background-color: white;
  box-sizing: border-box;
}
.main-container {
  padding-left: 40px;
  padding-right: 40px;
}
.block-sizer {
  max-width: 1800px;
  margin: auto;
}
.content {
  position: absolute;
  width: 900px;
  left: 50%;
  margin-left: -450px;
  top: 50%;
  transform: translateY(-50%);
  height: 100%;
  display: flex;
  align-items: center;
  z-index: 5;
}
a {
  text-decoration: none;
}
.red-dot {
  color: #e0392d;
}
@keyframes loading {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes error {
  0% {
    opacity: 0;
  }
  100% {
    fill: blue;
  }
}
@keyframes screen-back {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  98% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.fil1 {
  fill: none;
}
.fil3 {
  fill: #DAE4EA;
}
.fil6 {
  fill: #DAE4EA;
}
.fil0 {
  fill: #E2ECF5;
}
.fil2 {
  fill: #F0F4F7;
}
.fil5 {
  fill: #F0F4F7;
}
.fil4 {
  fill: #F0F4F7;
  fill-rule: nonzero;
}
#screen:hover #screen-on {
  animation: error;
  animation-delay: 4s;
  animation-fill-mode: forwards;
}
#screen:hover #screen-back {
  animation: screen-back;
  animation-duration: 4s;
  animation-fill-mode: forwards;
  fill: #696969;
}
#screen:hover #switch-on {
  fill: #00ED3B;
}
#screen:hover #loading {
  fill: white;
  animation: loading;
  animation-duration: 2s;
  animation-iteration-count: 2;
  animation-fill-mode: forwards;
}
.page-header__site-list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}
.page-header__site-list li {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
  margin-right: 40px;
}
.page-header__site-list li:last-child {
  margin-right: 0;
}
.page-header__nav {
  background-color: white;
  top: 0;
  right: 0;
  position: fixed;
  padding-right: 40px;
  padding-top: 40px;
  padding-left: 60px;
  padding-bottom: 40px;
  border-bottom-left-radius: 20px;
  z-index: 20;
  transition: top 1s;
}
.page_header__logo {
  position: fixed;
  top: 30px;
  left: 40px;
  z-index: 30;
}
.page_header__logo-circle {
  position: absolute;
  display: block;
  width: 90px;
  height: 90px;
  top: -23px;
  left: -10px;
  border-radius: 50%;
  z-index: 14;
  background-color: white;
}
.page_header__logo-img {
  position: relative;
  z-index: 15;
  opacity: 0.8;
}
.page_header__logo-img:hover {
  opacity: 1;
}
.page-header__site-list a {
  text-transform: uppercase;
  color: #000000;
  opacity: 0.8;
}
.page-header__site-list a:hover {
  opacity: 1;
}
.page-header__site-list--night a {
  color: white;
}
.site-list__item--current a {
  color: #e0392d;
  opacity: 1;
}
.site-list__item--current a--night {
  fill: white;
}
.main-block {
  height: 100vh;
  min-height: 700px;
  background-color: #ffffff;
  box-sizing: border-box;
  overflow: hidden;
  max-height: 1280px;
  position: relative;
  border-radius: 20px;
  padding-bottom: 40px;
  bottom: 20px;
}
.main-block__background {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
}
.main-block__background-image {
  position: relative;
  transform: translateY(780px);
}
.main-block__me-container {
  height: 100%;
  position: relative;
  vertical-align: middle;
}
.main-block__me-image {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.main-block__greeting {
  padding-left: 40px;
}
.main-block__greeting-title {
  margin: 0;
  padding: 0;
  font-family: "Comfortaa", Arial, sans-serif;
  font-weight: 700;
  font-size: 38px;
  line-height: 48px;
  margin-bottom: 25px;
}
.main-block__greeting-text {
  margin: 0;
  padding: 0;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  padding-right: 250px;
  margin-bottom: 45px;
  color: #6b6a6a;
}
.main-block__btn-lang {
  position: absolute;
  bottom: 50px;
  right: 50px;
  color: #000000;
  font-family: "Roboto", Arial, sans-serif;
  font-size: 16px;
  opacity: 0.7;
  z-index: 1;
}
.main-block__btn-lang:hover {
  opacity: 1;
}
.main-block__sun-night::before {
  content: "";
  position: absolute;
  display: block;
  background-image: url("../img/sun-night.svg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 56px;
  height: 28px;
  left: 50px;
  bottom: 50px;
  opacity: 0.7;
  z-index: 2;
}
.main-block__sun-night:hover::before {
  opacity: 1;
  cursor: pointer;
}
.main-block__night-switch:checked + .main-block__sun-night::before {
  background-position: 0 -29px;
}
.main-block--night,
.main-container--night,
.main-block__container--night,
.page_header__logo-circle--night,
.page-header__nav--night {
  background-color: #2C4860;
}
.main-block__background--night {
  filter: brightness(60%);
}
.best-portfolio {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  height: 150%;
  flex-wrap: wrap;
}
.best-portfolio__container {
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
  box-sizing: border-box;
  width: calc(50% - 10px);
  height: 550px;
  margin-right: 20px;
  margin-bottom: 20px;
  border-radius: 20px;
  overflow: hidden;
}
.best-portfolio__container:nth-child(2n) {
  margin-right: 0;
}
.best-portfolio__overlay {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 60px;
  z-index: -1;
}
.best-portfolio__overlay--image-1 {
  background-color: rgba(148, 163, 10, 0.9);
}
.best-portfolio__overlay--image-2 {
  background-color: rgba(227, 90, 36, 0.9);
}
.best-portfolio__overlay--image-3 {
  background-color: rgba(25, 79, 103, 0.9);
}
.best-portfolio__overlay--image-4 {
  background-color: rgba(0, 105, 91, 0.9);
}
.best-portfolio__overlay--image-5 {
  background-color: rgba(35, 35, 35, 0.9);
}
.best-portfolio__overlay--image-6 {
  background-color: rgba(224, 57, 45, 0.9);
}
.best-portfolio__tools {
  margin-top: auto;
}
.btn-action {
  background-color: transparent;
  box-sizing: border-box;
  padding: 15px 35px 15px 35px;
  border: 2px solid #ffffff;
  opacity: 0.5;
  display: inline-block;
  color: #ffffff;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  padding: 15px 50px 15px 50px;
  border-radius: 50px;
}
.btn-action:hover {
  opacity: 1;
}
.best-portfolio__image {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  border-radius: 20px;
}
.best-portfolio__container:hover .best-portfolio__overlay {
  z-index: 10;
}
.best-portfolio__image--image-1 {
  background-image: url("../img/best-works-img/image-1-main.jpg");
}
.best-portfolio__image--image-2 {
  background-image: url("../img/best-works-img/image-2-main.jpg");
}
.portfolio-block {
  display: flex;
  padding-top: 130px;
}
.portfolio-content__image {
  width: 9.267%;
  margin-bottom: 10px;
  margin-right: 5px;
  margin-left: 5px;
  min-width: 250px;
  border-radius: 5px;
  overflow: hidden;
  flex-grow: 2;
  opacity: 1;
  display: none;
}
.portfolio-content__image:hover {
  opacity: 0.8;
}
.portfolio-content__image::before {
  content: "";
  padding-top: 100%;
  float: left;
}
.portfolio-content {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  margin-left: 110px;
}
.portfolio-content::after {
  content: '';
  flex-grow: 5;
}
.portfolio-filter {
  position: fixed;
  display: flex;
  flex-direction: column;
}
.portfolio-filter__tab {
  margin-bottom: 20px;
  position: relative;
  margin-right: 20px;
  cursor: pointer;
}
.portfolio-filter__tab::before {
  content: '';
  display: block;
  height: 90px;
  width: 90px;
  border-radius: 5px;
  opacity: 0.5;
  transition-duration: 0.3s;
}
.portfolio-filter__tab:hover::before {
  opacity: 0.7;
}
.portfolio-filter__tab--vector::before {
  background-color: #e0392d;
}
.portfolio-filter__tab--raster::before {
  background-color: #94a30a;
}
.portfolio-filter__tab--advert::before {
  background-color: #194f67;
}
.portfolio-filter__tab--3d::before {
  background-color: #00695b;
}
.portfolio-filter__tab--tab-web::before {
  background-color: #e35a24;
}
.portfolio-filter__tab::after {
  content: '';
  position: absolute;
  width: 50px;
  background-size: cover;
  background-repeat: no-repeat;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.portfolio-filter__tab--vector::after {
  height: 34px;
  background-image: url("../img/vector-icon.svg");
}
.portfolio-filter__tab--raster::after {
  height: 39px;
  background-image: url("../img/raster-icon.svg");
}
.portfolio-filter__tab--advert::after {
  height: 48px;
  background-image: url("../img/ad-icon.svg");
}
.portfolio-filter__tab--3d::after {
  height: 50px;
  background-image: url("../img/3d-icon.svg");
}
.portfolio-filter__tab--tab-web::after {
  height: 39px;
  background-image: url("../img/web-icon.svg");
}
.portfolio-filter__checkbox:checked + .portfolio-filter__tab::before {
  opacity: 1;
  transition-duration: 0.3s;
}
.portfolio-content .portfolio-content__image--dummy {
  display: block;
  height: 2px;
}
.overlay {
  display: none;
  position: fixed;
  z-index: 20;
  box-sizing: border-box;
  padding: 50px;
  padding-right: 70px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.9);
}
.big-picture__cancel {
  list-style: none;
  margin: 0;
  padding: 0;
  border: none;
  position: fixed;
  display: block;
  width: 25px;
  height: 25px;
  background-color: rgba(0, 0, 0, 0);
  top: 20px;
  right: 30px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../img/close.svg");
  transition: transform 0.5s, opacity 0.5s;
  opacity: 0.5;
}
.big-picture__cancel:hover {
  transform: rotate(90deg);
  opacity: 1;
  cursor: pointer;
}
.big-picture__img {
  margin: auto;
  animation-name: zoom;
  animation-duration: 0.6s;
}
@keyframes zoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
.show {
  display: block;
}
